 * name: HouseRoomItem
 * description:
 * author: roct
 * date: 17:05 2021/4/18 0018
 */
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class HouseRoomItem extends Vue {
  // 当前楼层的信息
  @Prop({
    type: Object,
    default: () => {
      return {}
    }
  })
  private floorItem!: any
  // 朝向列表
  @Prop() private towardList!: any[]
  // 清空
  @Prop() private clear!: boolean

  @Watch('clear', {immediate: true, deep: true})
  ChangeClear(newVal: boolean) {
    if (newVal) {
      this.cancelAllSelect()
    }
  }
  // 全选本层
  private radio = false
  /**
   * @Author roct
   * @Description 监听全选本层
   * @Date 14:36 2021/6/4 0004
   **/
  private selectAllFloorClick() {
    // console.log('radio', this.radio)
    if (this.radio) {
      this.allSelect()
    } else {
      this.cancelAllSelect()
    }
  }
  /**
   * @Author roct
   * @Description 快速新增楼层
   * @Date 17:28 2021/4/18 0018
   **/
  private fastAdd() {
    this.$emit('fastAdd', this.floorItem.floor)
  }
  public created(): void {}
  public mounted(): void {}

  /**
   * @Author roct
   * @Description 获取朝向数据
   * @Date 18:40 2021/4/24 0024
   **/
  get toward() {
    return (item: any) => {
      if (!this.towardList || !item) {
        return ''
      }
      const t = this.towardList.find((t: any) => {
        return t.value === item.toward
      })
      if (t) {
        return t.name
      }
      return ''
    }
  }
  /**
   * @Author roct
   * @Description 点击全选
   * @Date 21:19 2021/4/19 0019
   **/
  private allSelect() {
    for (const item of this.floorItem.roomList) {
      item.selected = true
    }
    this.$emit('allSelect', this.floorItem)
    // console.log('roomList', this.floorItem.roomList)
  }
  /**
   * @Author roct
   * @Description 取消全选
   * @Date 21:19 2021/4/19 0019
   **/
  private cancelAllSelect() {
    for (const item of this.floorItem.roomList) {
      item.selected = false
    }
    this.$emit('allSelect', this.floorItem)
  }
  /**
   * @Author roct
   * @Description 点击选中
   * @Date 21:45 2021/4/19 0019
   **/
  private selectedItemClick(item: any) {
    if (!item.selected) {
      this.radio = false
    } else {
      const noSelectedItem = this.floorItem.roomList.find((i: any) => {
        return !i.selected
      })
      if (noSelectedItem) {
        this.radio = false
      } else {
        this.radio = true
      }
    }
    this.$emit('selectedItemClick', item)
  }
}
